import { BrowserModule } from "@angular/platform-browser";
import { NgModule, ErrorHandler } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { AppRoutingModule } from "./app-routing.module";

import { AppComponent } from "./app.component";
import { SharedModule } from "./shared/shared.module";
import { Configuration, ApiModule } from "./api";
import { AuthenticationService } from "./global/services/authentication.service";
import { ErrorsComponent } from "./global/components/errors/errors.component";
import { LoginComponent } from "./global/components/login/login.component";
import { EditorComponent } from "./global/components/editor/editor.component";
import { PasswordComponent } from "./global/components/password/password.component";
import { EmailValidationComponent} from "./global/components/email";

import { LogoutComponent } from "./global/components/logout/logout.component";
import { ErrorsHandler } from "./global/components/class/errors-handler";
import { HttpClientModule } from "@angular/common/http";
import { ServerErrorsInterceptor } from "./global/components/class/server-errors.interceptor";
import { ErrorsService } from "./global/services/errors.service";
import { NotificationService } from "./global/services/notification.service";
// https://github.com/AlexKhymenko/ngx-permissions
// import { NgxPermissionsModule } from "ngx-permissions";
import { GoogleAnalyticsService } from "./global/services/google-analytics.service";
import { ExternalLoginComponent } from "./global/components/external-login/external-login.component";
import { FacturationComponent } from "./global/components/facturation/facturation.component";
// https://github.com/drozhzhin-n-e/ng2-tooltip-directive

import { BrowserAnimationsModule } from "@angular/platform-browser/animations"; // this is needed by TagInputModule
import { ModuleService } from "./global/services/module.service";
import { AppService } from "./global/services/app.service";
import { APP_WIFEO } from "./shared/classes/CfgModule";

@NgModule({
    declarations: [
        AppComponent,
        LoginComponent,
        EditorComponent,
        PasswordComponent,
        EmailValidationComponent,
        LogoutComponent,
        ErrorsComponent,
        ExternalLoginComponent,
        FacturationComponent,
    ],
    imports: [
        BrowserModule,
        BrowserAnimationsModule,
        AppRoutingModule,
        FormsModule,
        ReactiveFormsModule,
        ApiModule,
        SharedModule.forRoot({ nameApp: APP_WIFEO.EDITOR }),
        // NgxPermissionsModule.forRoot()
    ],
    providers: [
        AuthenticationService,
        ErrorsService,
        NotificationService,
        GoogleAnalyticsService,
        AppService,
        ModuleService,
        {
            provide: ErrorHandler,
            useClass: ErrorsHandler,
        },
        {
            provide: Configuration,
            useFactory: (authService: AuthenticationService) =>
                new Configuration({
                    accessToken: authService.GetAccessToken(),
                    apiKeys: authService.getApiKeys(),
                    basePath: authService.getBasePath(),
                }),
            deps: [AuthenticationService],
            multi: false,
        },
    ],
    bootstrap: [AppComponent],
})
export class AppModule {}

