import { Component, OnInit, ElementRef, ViewChild } from "@angular/core";
import { environment } from "../../../../environments/environment";
import { StoreService } from "../../../shared/services/store.service";

@Component({
    selector: "app-external-login",
    templateUrl: "./external-login.component.html",
    styleUrls: ["./external-login.component.scss"]
})
export class ExternalLoginComponent implements OnInit {
    @ViewChild("iframeContent") app_iframe: ElementRef;
    srcIframe = "";
    constructor() {}

    ngOnInit() {
        const { scheme, pathApps, domain, apiProd, production } = environment;
        if (production) {
            this.srcIframe = `${scheme}://${pathApps}/external_login`;
        } else {
            this.srcIframe = `http://${pathApps}/external_login`;
        }

        this.srcIframe =
            this.srcIframe + "/external_login?v=" + environment.versionFiles;

        function receiveMessage(event) {
            // console.log(event, event.origin);

            if (!apiProd) {
                if (`${scheme}://www.${domain}` !== event.origin) {
                    return;
                }
            } else {
                if (event.origin !== `https://www.wifeocms.com`) {
                    return;
                }
            }

            let msg = JSON.parse(event.data);

            if (msg && msg.token && msg.email) {
                localStorage.setItem("currentUser", msg.token);
                // localStorage.setItem("emailUser", msg.email);
                let strMsg = JSON.stringify({
                    token: localStorage.getItem("currentUser")
                });
                document
                    .getElementById("iframeContent")
                    ["contentWindow"].postMessage(strMsg, "*");
            }
        }

        window.addEventListener("message", receiveMessage, false);
    }
}



