import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { AuthenticationService } from "../../services/authentication.service";
import { slug } from "../../../shared/utils/utils";
import { ErrorsService } from "../../services/errors.service";
import "rxjs/add/operator/finally";

declare var axios: any;
declare var $: any;

@Component({
    moduleId: module.id,
    templateUrl: "email.component.html"
})
export class EmailValidationComponent implements OnInit {

    token = "";
    error = "";
    returnUrl: string;

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private authenticationService: AuthenticationService,
        private errService: ErrorsService
    ) {
        this.route.params.subscribe(token => {
            this.token = token.params;
        });
    }



    emailVerify() {
        this.authenticationService.verifyEmailValidation(this.token).subscribe(e => {
            if (e && typeof e['status'] != "undefined") {
                localStorage.removeItem("accountValidationInProgress");
                if (e['status']) {
                    //console.log("verify ", e);
                    if (e['status'] == "expired") {
                        alert("Le lien d'activation est périmé. Veuillez renouveler la procédure de validation.");
                    } else if (e['status'] && typeof e['token'] != "undefined") {
                        localStorage.setItem("currentUser", e['token']);
                        this.router.navigateByUrl("/dashboard");
                    }

                }else{
                    alert("Lien d'activation inconnu.");
                }
            }
        }, fail =>{
            alert("Lien d'activation inconnu.");
        });
    }

    ngOnInit() {
        this.returnUrl = this.route.snapshot.queryParams["returnUrl"] || "/";
    }


}



