import { Component, OnInit, ChangeDetectorRef } from "@angular/core";
import { NotificationService } from "./global/services/notification.service";
import { ReactService } from "./shared/services/react.service";
import { Router } from "@angular/router";
import { environment } from "../environments/environment";

import { ErrorsService } from "./global/services/errors.service";
import { ScriptService } from "./shared/services/script.service";

@Component({
    selector: "app-root",
    templateUrl: "./app.component.html",
    styleUrls: ["./app.component.scss"]
})
export class AppComponent implements OnInit {
    notification: string;
    showNotification: boolean = false;

    reactJs = "ReactProd";
    reactDomJs = "ReactDomProd";

    constructor(
        private notificationService: NotificationService,
        private errorsService: ErrorsService,
        private cd: ChangeDetectorRef,
        public router: Router,
        private scriptService: ScriptService
    ) {}

    async ngOnInit() {
        this.initInterceptorAxios();
        this.initInterceptorJquery();
        await this.initReact();
        if (window) {
            window["__ENV_MODE"] = Object.freeze(environment);
        }

        function receiveMessage(event) {
            // Do we trust the sender of this message?
            const { scheme, pathApps, apiProd } = environment;
            if (!apiProd) {
                if (
                    event.origin !== `https://${pathApps}` &&
                    event.origin !== `http://${pathApps}`
                ) {
                    return;
                }
            } else {
                if (event.origin !== `https://admin.wifeosite.com`) {
                    return;
                }
            }
            if (typeof event.data !== "string") {
                return;
            }
            let msg = JSON.parse(event.data);

            window["ngModuleEditor"].zone.run(() => {
                window["ngModuleEditor"].component.runFromOutside(msg);
            });
        }

        window.addEventListener("message", receiveMessage, false);

        this.notificationService.notification.subscribe(message => {
            this.notification = message;
            this.showNotification = true;
            this.cd.detectChanges();
        });

        //this.scriptService.loader("Plupload");
    }

    async initReact() {
        if (!environment.reactProd) {
            this.reactJs = "ReactDev";
            this.reactDomJs = "ReactDomDev";
        }
        await this.scriptService.load("js", this.reactJs).then(async () => {
            await this.scriptService.load("js", this.reactDomJs);
        });
    }

    initInterceptorAxios() {
        window["axios"].interceptors.response.use(
            response => {
                // Do something with response data
                return response;
            },
            error => {
                // https://github.com/axios/axios#handling-errors
                this.errorsService.handleErrorHttp(error.response);
                return Promise.reject(error);
            }
        );
    }

    initInterceptorJquery() {
        window["$"].ajaxSetup({
            ajaxError: function() {
                // console.log("pouete");
            }
        });
    }
}

