import { Component, OnInit, ViewChild, ElementRef } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { AuthenticationService } from "../../services/authentication.service";
import { slug } from "../../../shared/utils/utils";
import { ErrorsService } from "../../services/errors.service";
import "rxjs/add/operator/finally";
import { environment } from "../../../../environments/environment";
import { ModalService } from "../../../shared/services/modal.service";

declare var axios: any;
declare var $: any;

@Component({
    moduleId: module.id,
    templateUrl: "login.component.html"
})
export class LoginComponent implements OnInit {
    model: any = {
        username: "",
        password: ""
    };

    loading = false;
    reset = false;
    loginAction = true;
    email_sended = false;
    error = "";
    returnUrl: string;

    @ViewChild("iframeContent") app_iframe: ElementRef;
    srcIframe = "";

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private authenticationService: AuthenticationService,
        private errService: ErrorsService,
        private modalService: ModalService
    ) { }

    onResetPassword() {
        this.reset = true;
    }

    get_browser() {
        var ua = navigator.userAgent, tem, M = ua.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || [];
        if (/trident/i.test(M[1])) {
            tem = /\brv[ :]+(\d+)/g.exec(ua) || [];
            return { name: 'IE', version: (tem[1] || '') };
        }
        if (M[1] === 'Chrome') {
            tem = ua.match(/\bOPR|Edge\/(\d+)/)
            if (tem != null) { return { name: 'Opera', version: tem[1] }; }
        }
        M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, '-?'];
        if ((tem = ua.match(/version\/(\d+)/i)) != null) { M.splice(1, 1, tem[1]); }
        return {
            name: M[0],
            version: M[1]
        };
    }



    ngOnInit() {
        const { scheme, pathApps, production } = environment;
        if (production) {
            this.srcIframe = `${scheme}://${pathApps}`;
        } else {
            this.srcIframe = `http://${pathApps}`;
        }

        this.srcIframe =
            this.srcIframe + "/external_login?v=" + environment.versionFiles;

        // reset login status
        this.authenticationService.logout();

        // get return url from route parameters or default to '/'
        const returnUrl = this.route.snapshot.queryParams["returnUrl"];
        this.returnUrl =
            (returnUrl !== "login" &&
                this.route.snapshot.queryParams["returnUrl"]) ||
            "/";
        this.modalService.close("modalErrorApp");


        var browser = this.get_browser(); 

        if (environment.production && environment.apiProd) {
            console.log("redirecting...");
            if(browser && browser.name.toLowerCase() == "safari"){
                console.log("safari stays here...", browser);
            }else{
                console.log("Browser...", browser);
                console.log("LOGIN : return wifeocms.com");
                //location.href = "https://www.wifeocms.com/se_connecter";
            }
            
        }
    }

    setAction(action) {
        this.loginAction = action === "login";
        this.error = "";
    }

    register() {
        this.loading = true;
        this.authenticationService
            .register(this.model.username1, this.model.password1)
            .finally(() => (this.loading = false))
            .subscribe(
                result => {
                    if (result["token"] && result["status"]) {
                        localStorage.setItem("currentUser", result["token"]);
                        // localStorage.setItem("emailUser", this.model.username1);
                        this.router.navigateByUrl("/", { replaceUrl: true });
                    } else {
                        this.error = "Username or password is incorrect";
                    }
                },
                error => {
                    this.errService.handleErrorHttp(error);
                    this.error =
                        (error.error && error.error.error) || "Erreur inconnue";
                }
            );
    }

    onEmailReceived() {
        this.email_sended = false;
        this.reset = false;
        this.loginAction = true;

        // redirect vers la landing page
    }

    resetting() {
        this.loading = true;
        this.authenticationService
            .resetting(this.model.username)
            .finally(() => (this.loading = false))
            .subscribe(
                result => {
                    this.email_sended = true;
                },
                error => {
                    this.errService.handleErrorHttp(error);
                    this.error =
                        (error.error && error.error.error) || "Erreur inconnue";
                }
            );
    }

    login() {
        this.loading = true;
        this.authenticationService
            .login(this.model.username, this.model.password)
            .finally(() => (this.loading = false))
            .subscribe(
                result => {
                    if (result["token"]) {
                        localStorage.setItem("currentUser", result["token"]);
                        // localStorage.setItem("emailUser", this.model.username);
                        let strMsg = JSON.stringify({
                            token: localStorage.getItem("currentUser")
                        });
                        this.app_iframe.nativeElement.contentWindow.postMessage(
                            strMsg,
                            "*"
                        );
                        // return;
                        // login successful so redirect to return url
                        // replaceUrl : supprime le path /login de l'historique de navigation

                        // location.href pour etre sur que les modules charge avec la bonne auth et ne pas tenté la première req avec un le token precedent..
                        if (this.returnUrl) {
                            location.href = this.returnUrl;
                        } else {
                            location.href = "/dashboard";
                        }

                        // this.router
                        //     .navigateByUrl(this.returnUrl, { replaceUrl: true })
                        //     .catch(e => {
                        //         this.router.navigate(["/dashboard"], {
                        //             replaceUrl: true
                        //         });
                        //     });
                    } else {
                        this.error = "Username or password is incorrect";
                    }
                },
                error => {
                    this.errService.handleErrorHttp(error);
                    this.error = "Username or password is incorrect";
                }
            );
    }
}



