import { Injectable } from "@angular/core";
import { Http, Headers, Response } from "@angular/http";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import "rxjs/add/operator/map";
import { Observable } from "rxjs/Observable";
import { Router } from "@angular/router";
import { environment } from "../../../environments/environment";
import { formatDomain } from "../../shared/utils/utils";

@Injectable()
export class AuthenticationService {
    public token: string;

    constructor(private http: HttpClient, private router: Router) {
        // set token if saved in local storage
        if (localStorage.getItem("currentUser")) {
            const currentUser = localStorage.getItem("currentUser");
            this.token = currentUser;
        }
    }

    /**
     * demande de challenge
     * @param token 
     */
    requestEmailValidation(): Observable<Object> {
        
        const url = "https://awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/user/api/send/validation/link ";
        const auth = this.getApiKeys();
        const headers = new HttpHeaders({
            'Content-Type': 'application/json',
            'Authorization': auth.Authorization
          });
          
        return this.http.get(url , { headers: headers });
    }


    /**
     * verif de l'email renvoi du challenge
     * @param token 
     */
    verifyEmailValidation(token: string): Observable<Object> {

        const url = "https://awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/user/api/verifies/"+token+"/validation/link";
        const auth = this.getApiKeys();
        const headers = new HttpHeaders({
            'Content-Type': 'application/json',
            'Authorization': auth.Authorization
          });
          
        return this.http.get(url , { headers: headers });
    }



    login(username: string, password: string): Observable<Object> {
        const url_str = "https://awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/user/login_check";
        const url = formatDomain(url_str);
        const body = new FormData();
        body.append("_username", username);
        body.append("_password", password);

        return this.http.post(url, body);
    }

    resetting(username: string): Observable<Object> {
        const url_str = "https://awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/user/resetting/send-email";
        const url = formatDomain(url_str);
        const body = new FormData();
        body.append("username", username);
        // body.append('_password', password);

        return this.http.post(url, body);
    }

    passwordUpdate(
        password: string,
        password2: string,
        id: string
    ): Observable<Object> {
        const url_str = "https://awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/user/resetting/reset/" + id;
        const url = formatDomain(url_str);
        const body = new FormData();
        body.append("fos_user_resetting_form[plainPassword][first]", password);
        body.append(
            "fos_user_resetting_form[plainPassword][second]",
            password2
        );

        return this.http.post(url, body);
    }

    register(username: string, password: string): Observable<Object> {
        const url_str = "https://awbjanv94g.execute-api.eu-west-1.amazonaws.com/prod/user/register";
        const url = formatDomain(url_str);

        const body = new FormData();
        body.append("_username", username);
        body.append("_password", password);

        return this.http.post(url, body);
    }

    logout(): void {
        // clear token remove user from local storage to log user out
        this.token = null;
        localStorage.removeItem("currentUser");
        localStorage.removeItem("emailUser");
        localStorage.removeItem("webSiteId");
        localStorage.removeItem("theme");
        localStorage.removeItem("accountValidationInProgress");
    }

    GetAccessToken() {
        if (localStorage.getItem("currentUser")) {
            const currentUser = localStorage.getItem("currentUser");
            this.token = currentUser;
        }
        return "Bearer " + this.token;
    }

    getApiKeys() {
        return {
            Authorization:
                "Bearer " +
                (this.token !== null
                    ? this.token
                    : localStorage.getItem("currentUser"))
        };
    }

    getBasePath() {
        let basePath = "";
        if (!environment.production) {
            basePath = "";
        }

        return basePath;
    }
}



