import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { AuthGuard } from "./shared/_guards";
import { ErrorsComponent } from "./global/components/errors/errors.component";
import { LoginComponent } from "./global/components/login/login.component";
import { EditorComponent } from "./global/components/editor/editor.component";

import { PasswordComponent } from "./global/components/password/password.component";

import { LogoutComponent } from "./global/components/logout/logout.component";
import { ExternalLoginComponent } from "./global/components/external-login/external-login.component";
import { EmailValidationComponent } from "./global/components/email/email.component";

import { FacturationComponent } from "./global/components/facturation/facturation.component";
// import { NgxPermissionsGuard } from 'ngx-permissions'; // if only admin module

const routes: Routes = [
    {
        path: "dashboard",
        loadChildren: "app/dashboard/dashboard.module#DashboardModule",
        canActivate: [AuthGuard]
    },
    { path: "email/validation/:params", component: EmailValidationComponent, canActivate: [AuthGuard]  },
    { path: "login", component: LoginComponent },
    { path: "editor", component: EditorComponent },
    { path: "password/reset/:params", component: PasswordComponent },
    { path: "logout", component: LogoutComponent, canActivate: [AuthGuard] },
    { path: "error", component: ErrorsComponent },
    { path: "external_login", component: ExternalLoginComponent },
    { path: "", redirectTo: "/dashboard/accueil", pathMatch: "full" },
    { path: "**", component: ErrorsComponent, data: { error: 404 } }
];

@NgModule({
    imports: [
        RouterModule.forRoot(
            routes
            // { enableTracing: true }
        )
    ],
    exports: [RouterModule],
    providers: []
})
export class AppRoutingModule {}

